  @import url('https://fonts.googleapis.com/css2?family=Gabarito:wght@400;700&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Playpen+Sans&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Display:wght@500&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Source+Sans+3:wght@500&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

  * {
    margin: 0;
    padding: 0;
    font-family: var(--font3);
    list-style: none;
    box-sizing: border-box;
    color: var(--white2);
    text-decoration: none;
  }

  :root {
    --font1: 'Gabarito', sans-serif;
    --font2: 'Ubuntu', sans-serif;
    --font3: 'Lato', sans-serif;
    --font4: 'Edu TAS Beginner', cursive;
    --font5: 'Noto Sans Display', sans-serif;
    --font6: 'Source Sans 3', sans-serif;
    --font7: 'Poppins', sans-serif;
    --blue2: #00e7ff;
    --blue1: #00dcff;
    --red1: #ff6c6ce8;
    --red2: #ffa4a4;
    --white1: #ECEBEE;
    --white2: #EAEAEA;
    --white3: #B0BFC2;
    --blackLight: #141517;
    --black1: #1C1D20;
    --black2: rgba(11, 12, 14, 0.671);
    --black3: rgba(0, 0, 0, 0.836);
    --borderLight : rgba(28, 29, 32, 0.175);
    --grey: #E9EAEB;
  }

  ::selection {
    color: var(--white1);
  }

  html::-webkit-scrollbar {
    display: none;
  }

  .root {
    position: relative;
  }

  body {
    background-color: #09090a;
    position: relative;
    overflow-x: hidden;
  }

  .container {
    width: 80%;
    margin: auto;
  }

  main {
    display: flex;
    flex-direction: column;
    row-gap: 12rem;
    position: relative;
    overflow: hidden;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .navLogo {
    cursor: pointer;
    height: 5rem;
    width: 5rem;
  }

  .navLogo img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  header {
    position: fixed;
    width: 100vw;
    z-index: 10;
  }

  header nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
  }

  header nav ul {
    display: flex;
    gap: 3rem;
    transition: transform 0.6s cubic-bezier(0.65, 0, 0.35, 1);
  }

  header nav ul li {
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    cursor: pointer;
    font-family: var(--font2);
    transition: all 100ms ease-in;
  }

  header nav ul li a {
    color: var(--white3);
  }

  .homeDiv {
    text-transform: uppercase;
  }

  .scroll {
    margin-top: 3rem;
  }

  .scroll svg {
    font-size: 1.5rem;
  }


  .homeDiv h1 {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }


  /* ! ================================================================================== SIDENAv =================================================================== */

  .sideNav {
    height: 100%;
    background: var(--blackLight);
    position: fixed;
    width: 35%;
    z-index: 10;
  }

  .innerSide{
    padding: 15vh 13vh 10vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20vh;
  }

  .NavSocial{
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .navIcons{
    display: flex;
    justify-content: space-between;
  } 

  .navIcons a{
    font-size: 1rem;
    font-family: var(--font5);
    transition: all 150ms ease-in-out;
  }

  .navIcons a:hover{
    scale: 1.2;
  }

  .upper{
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }
  .name{
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .upper ul{
    display: flex;
    gap: 0.2rem;
    flex-direction: column;
  }

  .upper ul li a{
    font-family: var(--font6);
    font-size: 4rem;
    display: flex;
    gap: 1rem;
    align-items: center;
  }
  
  .options li{
    transition: all 150ms ease-in-out;
    display: flex;
    gap: 1rem;
    align-items: center;
  }
 

  .name h3{
    font-size: 1rem;
    opacity: 0.4;
    font-family: var(--font2);
    letter-spacing: 1px;
  }

  .line{
    height: 1px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.2);
  }

  /* ?=============================================== HAMBURGER ========================================================= */


  .hamBurger {
    position: fixed;
    width: 4rem;
    margin-top: 1rem;
    height: 4rem;
    right: 4%;
    border-radius: 50%;
    background: var(--white1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 11;
  }

  .lines {
    display: flex;
    gap: 0.5em;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  .lines:hover div:nth-child(1) {
    transform: rotate(45deg);
  }

  .lines:hover div:nth-child(2) {
    transform: rotate(-45deg);
  }

  .hamLine {
    background: var(--blackLight);
    width: 40%;
    transition: all 150ms ease-in-out;
    height: 2px;
  }

  .circleHide{
    display: none;
  }
  .active .circleHam{
    background: var(--white1);
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
  }


  /* =============================================== SOCIAL ========================================================= */
  .SocialDiv {
    position: fixed;
    z-index: 11;
    top: 75%;
    left: 2rem;
    display: flex;
    gap: 1rem;
    overflow: hidden;
    flex-direction: column;
  }

  .SocialDiv a {
    position: relative;
    transition: all 150ms ease-in-out;
  }

  .SocialDiv a:hover {
    transform: scale(1.1);
  }

  .SocialDiv svg {
    font-size: 2rem;
  }

  .SocialDiv svg path {
    color: var(--white1);
  }


  .cursor {
    position: fixed;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background-color: rgba(214, 214, 214, 0.2);
    box-shadow: rgba(240, 240, 240) 0.35 0px 5px 15px;
    ;
    transform-origin: center center;
    pointer-events: none;
    z-index: 20;
  }

  .cursor span {
    display: none;
    font-size: 0.5rem;
  }

  .grow {
    transform: scale(7);
    mix-blend-mode: difference;
    background: white;
  }

  .imgGrow span {
    display: flex;
    color: var(--white1);
    z-index: 101;
    font-size: 0.15rem;
  }

  .imgGrow {
    transform: scale(7);
    background: #09090a;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .LoaderDiv {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: black;
    z-index: 20;
  }

  /* =============================================== Swipper ========================================================= */

  .swiper {
    width: 100%;
    height: 100%;
  }

  .swiper-wrapper {
    overflow: visible !important;

  }

  .swiper-slide::after {
    content: '';
    z-index: 10;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(1, 1, 32, 0.5) 45%, rgba(0, 0, 0, 0.9) 88%);
  }


  .swiper-pagination-bullet-active {
    background: var(--white2) !important;
  }

  .swiper-slide {
    width: 35rem !important;
    height: 30rem !important;
    overflow: visible !important;
    position: relative;

  }

  .swiper-slide img {
    display: block;
    width: 100%;
    filter: saturate(0);
    height: 100%;
    object-fit: fill;
  }

  .swiper-button-prev,
  .swiper-button-next {
    color: var(--white3) !important;
  }

  /* =============================================== Projects ========================================================= */

  .projects {
    flex-grow: 1;
    display: flex;
    gap: 5rem;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: auto;
  }

  .ProjectDiv h1 {
    margin-top: 15rem;
    transform: scale(1);
  }

  .pjBtn {
    background-color: var(---black1);
    box-shadow: rgba(0, 0, 0, 0.84) 5px 2px 21px;
  }



  /* =============================================== Skills ========================================================= */

  .line1 {
    height: 30%;
  }

  .line2 {
    height: 60%;
  }

  .line3 {
    height: 40%;
  }

  .contactHead {
    overflow: hidden;
  }

  .formBtn {
    background: none;
    padding: 1rem;
    border: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    border-radius: 1rem;
    background: var(---black2);
    transition: all 150ms ease-in-out;
    cursor: pointer;
  }

  .formBtn:hover {
    background: var(---black3);
    transform: scale(1.1);
    border: none;
  }


  /* ============================================================================== AStro ======================================================== */

  .astro {
    width: 500vw;
    height: 100vh;
    position: absolute;
    display: flex;
    align-items: center;
  }

  #motionPath {
    overflow: visible;
    height: 100%;
    width: 80%;
    position: relative;
  }

  #motionPath path {
    stroke-width: 2;
    stroke: gray;
  }

  .spline {
    height: 100vh !important;
    width: 100vw !important;
    position: fixed;
    filter: saturate(0);
    pointer-events: none;
    z-index: -1;
    opacity: 0.1;
  }


  /* * ====================================================================== Button =============================================== */

  .workButton{
    height: 5rem;
    width: 15rem;
    margin: 0 0 5rem 0;
    overflow: hidden;
    border-radius: 5rem;
    position: relative;
  }

  .workButton .slider{
    width: 100%;
    height: 100%;
    position: relative;
  }

  .workButton .btnWrk{
    height: 100%;
    width: 100%;
    display: flex;
    background: var(--black2);
    justify-content: center;
    align-items: center;
  }

  .workButton .btnWrk a{
    font-size: 1.5rem;
    font-weight: 600;
    letter-spacing: 1px;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-family: var(--font2);
  }

  .workButton .btnWrk:nth-child(2){
    position: absolute;
    top: 100%;
    background: var(--white2);
  }

  .workButton .btnWrk:nth-child(2) a{
    color: black;
    letter-spacing: 1px;
    font-weight: 600;
  }


  .titleColumn{
    display: flex;
    gap: 5vh;
    padding: 0 20vh;
  }

  .titleName:nth-child(1){
    padding: 1.5rem 2rem;
  }

  .titleName{
    padding: 1.5rem;
    border-radius: 2.5rem;
    cursor: pointer;
    background: white;
    border: 1px solid var(--blackLight);
  }

  .titleName span{
    color: var(--black1);
    font-size: 1rem;
    font-weight: 600;
  }