.wrapper {
    position: relative;
    height: 180vh;
    display: flex;
    gap: 2vw;
    margin-top: 50vh;
    box-sizing: border-box;
    padding: 1rem;
    justify-content: center;
    overflow: hidden;
}

.column{
    width: 25%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 2vw;
    min-width: 15rem;
    position: relative;
}

.wrapper :nth-child(1){
    top: -40%;
}
.wrapper :nth-child(2){
    top: -80%;
}
.wrapper :nth-child(3){
    top: -45%;
}
.wrapper :nth-child(4){
    top: -75%;
}

.imageContainer{
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 0.75rem;
}

.imageContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}






/* *============================================================================== Circle ================================================================ */

.circleContainer{
    position: relative;
    margin-top: 5rem;
}

.circle{
    background-color: #09090a;
    width: 140%;
    height: 700%;
    left: -20%;
    position: absolute;
    z-index: 1;
    border-radius: 0% 0% 50% 50%;
}
